@use '/src/assets/css/variables' as *;

.news-block {
    width: 100%;
    //border: 1px solid red;
    padding-top: 20px;
    padding-bottom: 40px;
    display: flex;
    align-items: stretch;    
    justify-content: center;
    flex-wrap: wrap;
    position: relative;
    min-height: 300px;

    @include respond-to(xs) {
        width: 100%;
    }

    & >  .break-new-line {
        width: 100%;
    }

    & .show-more-news {
        margin-top: 30px;
        
        & span {
            color: var(--text);
            font-weight: 700;
            font-size: 18px;
            margin: 0 30px;
        }

        & svg {
            fill: var(--text);
            height: 20px;
        }

    }
}